const ProductsApollo = compose(
  withApollo,
  graphql(VENDORS_QUERY, {
    props: ({ data: { loading, error, vendors } }) => {
      if (error) throw new Error(`Error in Vendors Query: ${error}`);
      return { loading, vendors };
    }
  })
)(Products);

export default ProductsApollo;
